@import url('https://fonts.googleapis.com/css2?family=Carter+One&family=Poppins:ital,wght@0,400;0,700;1,400&display=swap');

$green: rgb(40, 217, 24);

.Navbar {
  display: grid;
  align-items: center;
  justify-content: center;
  grid-template-columns: 1fr 1.5fr;
  padding: 20px 40px;
  background-color: black;

  .logo {
    button {
      font-family: 'Carter One', cursive;
      border: none;
      font-size: 25px;
      background-color: black;
      color: white;
      cursor: pointer;
    }
  }

  #nav {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5vw;

    .cart-icon {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 10px;
      color: white;

      #cart-number {
        display: flex;
        align-items: center;
        justify-content: center;
        color: black;
        background-color: $green;
        border: none;
        border-radius: 60%;
        width: 30px;
        height: 28px;
        font-size: 20px;
        font-weight: bold;
      }
    }

    button {
      position: relative;
      font-family: 'Poppins', sans-serif;
      font-size: 20px;
      color: white;
      background-color: black;
      border: none;
      cursor: pointer;
      text-decoration: none;
    }

    #links {
      &:after {
        position: absolute;
        content: '';
        background-color: rgb(40, 217, 24);
        height: 3px;
        width: 0;
        left: 0;
        bottom: -5px;
        transition: 0.3s;
      }

      &:hover:after {
        width: 100%;
      }
    }
  }
}

// Media Queries
@media (max-width: 500px) {
  .Navbar {
    padding: 20px 15px;
    grid-template-columns: 0.5fr 1fr;

    .logo {
      button {
        font-size: 15px;
      }
    }

    #nav {
      gap: 15px;

      .cart-icon {
        gap: 5px;
        #cart-number {
          width: 15px;
          height: 14px;
          font-size: 10px;
        }
      }

      button {
        font-size: 12px;
      }
    }
  }
}
