.Shopping-Cart-Container {
  position: fixed;
  border: 2px solid white;
  top: 0%;
  left: 65%;
  height: 100vh;
  width: 35vw;
  z-index: 1000;
  background-color: white;
  padding: 20px 20px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  overflow: auto;
}
.Shopping-Cart-Container .close {
  display: flex;
  justify-content: space-between;
}
.Shopping-Cart-Container .close #close-button {
  background-color: white;
  color: black;
  font-size: 40px;
}
.Shopping-Cart-Container .close #close-button:hover {
  color: rgb(203, 33, 33);
  transition: 0.3s;
}
.Shopping-Cart-Container #empty {
  margin: 30px 0px;
  font-size: 30px;
  font-style: italic;
}
.Shopping-Cart-Container .Shopping-Cart-Checkout #total {
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 5px;
}
.Shopping-Cart-Container .Shopping-Cart-Checkout #checkout-button {
  padding: 5px 20px;
  font-size: 30px;
  border-radius: 5%;
}
.Shopping-Cart-Container .Shopping-Cart-Checkout #checkout-button:hover {
  background-color: rgb(51, 172, 14);
  transition: 0.5s;
}

@media (max-width: 500px) {
  .Shopping-Cart-Container {
    left: 30%;
    width: 70vw;
  }
  .Shopping-Cart-Container .Shopping-Cart-Checkout {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .Shopping-Cart-Container .Shopping-Cart-Checkout #total {
    margin-top: 20px;
    font-size: 24px;
  }
}/*# sourceMappingURL=ShoppingCart.css.map */